.desktop-nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    &.flow {
        position: fixed;
        transform:  translateY(-90px);
        z-index: 99;
        background-color: #090e0e;
        box-shadow: 0 3px 7px #0000007a;
    }
    &.show {
        transform: translateY(0px);
        transition: .5s;
    }
    .container {
        display: flex;
        padding: 1.25rem 0;
        img {
            height: 2.813rem;
            margin-right: 6rem;
            filter: brightness(50);
        }
        ul {
            width: 50%;
            display: flex;
            align-items: center;
            @media (min-width: 62rem) {
                & {
                    margin-left: auto;
                }
            }
            li {
                margin: 0 1.25rem;
                transition: .5s;
                @media (min-width: 62rem) {
                    & {
                        margin: 0 0.5rem;
                    }
                }
                @media (min-width: 75rem) {
                    & {
                        margin: 0 1.5rem;
                    }
                }
                a {
                    color: $Primary_Text;
                    font-size: 0.875rem;
                    white-space: nowrap;
                    font-family: 'Changa', sans-serif;
                    position: relative;
                }
                a::after {
                    content: '';
                    position: absolute;
                    bottom: 0.313rem;
                    left: 0;
                    height: 0.125rem;
                    width: 0;
                    background-color: #41c6c6;
                    transition: .5s;
                }
            }
            li:hover:not(li:last-child) a::after,
            li.active:not(li:last-child) a::after{
                width: 100%;
            }
            li:last-child {
                margin-left: 2.4rem;
                a {
                    color: $Secondary_Text;
                    background-color: $Primary_Text;
                    padding: .45rem 1.65rem;
                    border-radius: 0.313rem;
                    transition: .5s;
                }
                a:hover {
                    background-color: transparent;
                    color: $Primary_Text;
                    outline: 1px solid $Primary_Text;
                }
            }
            li:first-child {
                margin-left: 1.5rem;
            }
        }
    }
}