.services {
    .container {
        .card-container {
            @extend .center-f;
            justify-content: center;
            flex-wrap: wrap;
            width: 100%;
            @media (min-width: 62rem) {
                &:nth-child(2) {
                    margin-top: 6rem;
                }
            }
            .card {
                width: 245px;
                height: 255px;
                margin: 10px;
                background-position: center;
                background-size: cover;
                position: relative;
                &:hover .text{
                    height: 100%;
                }
                @media (min-width: 62rem) {
                    & {
                        width: 280px;
                        height: 270px;
                        margin: 0 25px;
                    }
                    &:nth-child(2) {
                        transform: translateY(-15px);
                    }
                    &:nth-child(3) {
                        transform: translateY(-30px);
                    }
                }
                .text {
                    @extend .center-g;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 20%;
                    font-size: 1rem;
                    letter-spacing: 1px;
                    font-family: 'Changa', sans-serif;
                    background-color: #253434a1;
                    color: #fff;
                    transition: .5s;
                }
            }
        }
    }
}